    align-items: center;
}

.icon-animation-body .icon-container .icon {
    width: 200px;
}

.icon-animation-body .icon-container .icon path {
    fill: transparent;
    stroke-width: 10;
    stroke: #31e8ff;
}

.icon-animation-body .icon-container .icon.heart path {
    stroke-dasharray: 1560;
    stroke-dashoffset: 0;
}

.icon-animation-body .icon-container .icon.heart:hover path {
    -webkit-animation: animate-heart 1.5s linear forwards;
            animation: animate-heart 1.5s linear forwards;
}

@-webkit-keyframes animate-heart {
    0% {
        stroke-dashoffset: 0;
    }
    40% {
        stroke-dashoffset: 1560;
    }
    80% {
        stroke-dashoffset: 3120;
        fill: transparent;
    }
    100% {
        stroke-dashoffset: 3120;
        fill: #31e8ff;
    }
}

@keyframes animate-heart {
    0% {
        stroke-dashoffset: 0;
    }
    40% {
        stroke-dashoffset: 1560;
    }
    80% {
        stroke-dashoffset: 3120;
        fill: transparent;
    }
    100% {
        stroke-dashoffset: 3120;
        fill: #31e8ff;
    }
}

