.image-clip-hover-card-demo .container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 20px;
}

.image-clip-hover-card-demo .container .box {
    position: relative;
    width: 400px;
    height: 400px;
    background: white;
    margin: 5px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 5px 15px;
}

.image-clip-hover-card-demo .container .box .content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: grid;
    align-items: center;
    color: black;
    padding: 20px;
    font-family: "Poppins", sans-serif;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.5) 0px 20px 50px inset;
}

