.image-clip-hover-card-demo .container .box .content p {
    margin-bottom: 25px;
}

.image-clip-hover-card-demo .container .box .content a {
    display: inline-block;
    margin-top: 10px;
    padding: 10px 20px;
    border: 1px solid white;
    text-decoration: none;
    color: black;
    transition: .5s;
}

.image-clip-hover-card-demo .container .box .content a:hover {
    background: black;
    color: white;
